.App {
  background-color: wheat;
  width: 100% ; /* Ancho del 100% del viewport */
  height: 100% ; /* Alto del 100% del viewport */
}
header {
  background-color: #6A605C;
  
  margin: 0 auto;
  width: 80%;
}
footer {
  background-color: #6A605C;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin: auto auto 0 auto;
}
.home-page{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.navbar {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding-top: 5px;
}
.text-wrapper-3 {
  margin-right: 5px; /* Agregar un espacio de 5px entre cada elemento */
}

.logo {
  border-radius: 4%;
  margin-bottom: 4px;
  
}

.header-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 10% 0 5% ;
}
.title{
  font-family: 'Cedarville Cursive', cursive;
 font-size: 300%;
 margin-top: 10px;
}

@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap');

.body-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  margin: auto;
}

.img {
  max-width: 45%; /* Asegura que la imagen no exceda el ancho del contenedor */
  height: auto; /* Permite que la altura se ajuste proporcionalmente */
  
  margin: 10px; /* Aumenta un poco el margen inferior para separar */
}

.aboutme-text{
  font-family: 'Crafty Girls', cursive;
  font-size: calc(1vh +5)
}

@import url('https://fonts.googleapis.com/css2?family=Crafty+Girls&display=swap');